@import '../../stylesheets/variables.scss';

.sidebar-container {
    height: 100%;
    width: 100%;
    min-width: 320px;
    max-width: 425px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.9);
    overflow-x: hidden;
    transition: 0.5s;

    .sidebar-content {
        position: relative;
        top: 25%;
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }

    .close-btn {
        position: absolute;
        top: 20px;
        right: 45px;

        padding: 8px;
        text-decoration: none;
        font-size: 36px;
        color: #818181;
        display: block;
        transition: 0.3s;
    }

    button {
        font-size: 36px;
        display: block;
        transition: 0.3s;
        text-align: center;
        margin: 8px auto;
        border: none;
        width: 100%;
        max-width: 300px;
        background-color: transparent;
    }

    .active{
        color: white;
        text-decoration: underline;
    }

    .not-active{
        color: #818181;
        text-decoration: none;

    }

    button:hover,
    .close-btn:hover,
    .close-btn:focus {
        color: #f1f1f1;
    }
}

.hide {
    display: none;
    width: 0;
}