@import '../variables.scss';

.header-container {
    box-sizing: border-box;
    position: fixed;
    top: 0;
    overflow: hidden;
    width: 100%;
    z-index: 1;

    display: flex;
    flex-direction: row;

    padding: 20px;
    background: #000;
    color: white;

    .logo-container {
        width: 15%;
        float: left;
        text-align: center;

        .icon {
            font-size: 2rem;
            color: pink;
        }

        img{
            width: 90%;
            max-width: 40px;
        }

    }

    .searchbar-container {
        width: 70%;
        float: none;
        text-align: center;

        display: flex;
        justify-content: center;

        input {
            text-align: center;
            padding: 6px;
            font-size: 1rem;
            border: none;
            width: 60%;
            min-width: 125px;
            max-width: 500px;
            border-top-left-radius: $border_radius;
            border-bottom-left-radius: $border_radius;

        }

        button {
            padding: 6px;
            background: white;
            font-size: 1rem;
            border: none;
            cursor: pointer;
            border-top-right-radius: $border_radius;
            border-bottom-right-radius: $border_radius;
        }

    }

    .cart-container {
        width: 15%;

        float: left;
        text-align: center;

        .icon-container{
            position: relative;
            width: 100%;
            max-width: 50px;

            .icon {
                font-size: 2rem;
                color: white;
            }
            span{
                position: absolute;
                bottom: -5px;
                right: 2px;
                background-color: whitesmoke;
                color: grey;
                font-weight: bold;
                width: 20px;
                height: 20px;
                border-radius: 50%;
            }
        }

        .icon-container:hover{
            .icon{
                color: lightgray;
            }
            .span{
                color: black;
            }
        }
    }

}